body {
  font-family: "Open Sans", "Arial", sans-serif;
  padding: 0;
  margin: 0;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  border: 0;
  clip: rect(0 0 0 0);
}
.header {
  padding-top: 20px;
  position: relative;
  background-color: #283645;
  z-index: 100;
}
.header__logo {
  margin-left: 20px;
}
.header__logo-image:hover {
  color: #D22856;
}
.header__logo-image:active {
  fill: white;
  opacity: 0.3;
}
@media (min-width: 660px) {
  .header {
    padding-top: 27px;
  }
  .header__logo {
    padding-bottom: 20px;
  }
}
@media (min-width: 1200px) {
  .header {
    padding-top: 0;
    display: flex;
    justify-content: flex-start;
    background-color: black;
    opacity: 0.3;
    z-index: 20;
  }
  .header__wrapper {
    padding: 0;
    margin: 0;
  }
  .header__logo {
    margin-left: 100px;
  }
}
.navigation {
  padding-top: 20px;
}
.navigation__toggle {
  position: absolute;
  top: 23px;
  right: 27px;
  width: 23px;
  height: 23px;
  border: none;
  background-color: transparent;
  cursor: pointer;
  fill: white;
}
.navigation__toggle:hover,
.navigation__toggle:focus {
  outline: none;
  fill: #D22856;
}
.navigation__toggle:active {
  outline: none;
}
.navigation__list {
  padding: 0;
  margin: 0;
  list-style: none;
  border-top: 1px solid black;
}
.navigation__item {
  border-top: 1px solid #485461;
  border-bottom: 1px solid #000000;
}
.navigation__link {
  padding-top: 21px;
  padding-bottom: 21px;
  display: block;
  color: white;
  text-decoration: none;
  font-family: "Open Sans Bold", "Arial", sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 18px;
  text-align: center;
  text-transform: uppercase;
}
.navigation__link:hover,
.navigation__link:focus {
  color: #D22856;
}
.navigation__link:active {
  color: white;
  opacity: 0.3;
}
@media (min-width: 660px) {
  .navigation {
    padding-top: 39px;
  }
  .navigation__toggle {
    top: 42px;
    right: 28px;
  }
}
@media (min-width: 1200px) {
  .navigation {
    padding: 0;
    margin-left: 200px;
  }
  .navigation__list {
    display: flex;
    border: none;
  }
  .navigation__item {
    border: none;
    margin-left: 50px;
  }
  .navigation__toggle {
    display: none;
  }
}
.intro {
  text-align: center;
  background-color: #1D2631;
}
.intro__heading {
  position: relative;
  padding: 0;
  margin: 0;
  font-family: "Open Sans", "Arial", sans-serif;
  font-style: normal;
  font-weight: 300;
  font-size: 24px;
  line-height: 120%;
  text-align: center;
  color: #ffffff;
  padding-top: 29px;
  padding-bottom: 300px;
  margin-bottom: 28px;
}
.intro__heading::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  top: -66px;
  left: 0;
  background: url("../img/back-mobile.jpg") no-repeat top center;
  background-size: cover;
  background-color: black;
  padding-top: 66px;
}
.intro__heading::after {
  content: "";
  position: absolute;
  display: block;
  bottom: 0;
  left: 50%;
  width: 320px;
  height: 286px;
  background: url("../img/phone-hand.png") no-repeat bottom center;
  margin-left: -160px;
}
.intro__heading-text {
  display: block;
  margin: 0 auto;
  font-family: "Open Sans Light";
  position: relative;
  z-index: 10;
  width: 250px;
  margin-bottom: 12px;
}
.intro__button {
  display: block;
  margin: 0 auto;
  width: 280px;
  font-family: "Open Sans Bold", "Arial", sans-serif;
  font-size: 18px;
  color: white;
  border: 3px solid white;
  border-radius: 29px;
  background-color: transparent;
  padding-top: 15px;
  padding-bottom: 15px;
  margin-bottom: 30px;
  cursor: pointer;
  text-decoration: none;
}
.intro__button:hover,
.intro__button:focus {
  outline: none;
  background-color: white;
  color: #D22856;
}
.intro__button:active {
  outline: none;
  color: rgba(210, 40, 86, 0.3);
}
.intro__list {
  padding: 0;
  margin: 0;
  list-style: none;
  display: inline-flex;
  justify-content: flex-start;
  align-items: center;
  width: 204px;
  margin-left: -71px;
  margin-bottom: 17px;
}
.intro__item {
  margin-left: 35px;
}
.intro__description {
  width: 280px;
  margin: 0 auto;
  font-family: "Open Sans Light", "Arial", sans-serif;
  font-size: 14px;
  line-height: 24px;
  color: white;
  text-align: center;
  padding-bottom: 32px;
}
