  margin-bottom: 50px;
}
.checkbox__input:hover + .checkbox__label::before {
  border-color: #283645;
}
.checkbox__input:focus + .checkbox__label::before {
  outline: none;
}
.checkbox__input:active + .checkbox__label::before {
  border-color: #D22856;
}
.checkbox__input:checked + .checkbox__label::after {
  display: block;
}
.checkbox__label-text {
  margin-left: 60px;
}
